
import React from "react";
import PhoneIphoneIcon from "@mui/icons-material/PhoneIphone";
import MailOutlineIcon from "@mui/icons-material/MailOutline";
import "./style.css";
const ContactMe = () => {
    return (
        <>
            <div id="contact">
                <h2 style={{ fontSize: "4rem" }}>Get in touch</h2>
                <div>
                    <p
                        style={{
                            display: "flex",
                            fontSize: "1.5rem",
                            justifyContent: "center",
                            alignItems: "center",
                        }}
                    >
                        <PhoneIphoneIcon style={{ color: "white", fontSize: "50px " }} />
                        9742373640
                    </p>
                    <p
                        style={{
                            display: "flex",
                            fontSize: "1.5rem",
                            justifyContent: "center",
                            alignItems: "center",
                        }}
                    >
                        <MailOutlineIcon style={{ color: "white", fontSize: "50px " }} />
                        anitha6472@gmail.com
                    </p>
                </div>
            </div>
        </>
    );
};
export default ContactMe;