
import * as React from "react";
import { styled } from "@mui/material/styles";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";
import Collapse from "@mui/material/Collapse";
import Avatar from "@mui/material/Avatar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import { red } from "@mui/material/colors";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
})(({ theme, expand }) => ({
    transform: !expand ? "rotate(0deg)" : "rotate(180deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
        duration: theme.transitions.duration.shortest,
    }),
}));

export default function Exp() {
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    return (
        <div id="Exp">
            <Card sx={{ maxWidth: 345 }}>
                <CardHeader
                    avatar={
                        <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                            S
                        </Avatar>
                    }
                    title="Simplotel technology pvt ltd"
                    subheader="September 2019 - June 2023"
                />
                <CardContent>
                    <Typography variant="body2" color="text.secondary">
                        Software Engineer"
                    </Typography>
                </CardContent>

                <CardActions disableSpacing>
                    <div
                        onClick={handleExpandClick}
                        aria-expanded={expanded}
                        aria-label="show more"
                        style={{
                            display: "flex",
                            alignItems: "center",
                            cursor: "pointer",
                            padding: "16px",
                            boxShadow: "0 1rem 3rem rgba(0, 0, 0, .175) !important",
                        }}
                    >
                        <p style={{ margin: 0 }}>Read more</p>
                        <ExpandMoreIcon
                            className={`expand-icon ${expanded ? "expanded" : ""}`}
                        />
                    </div>
                </CardActions>
                <Collapse in={expanded} timeout="auto" unmountOnExit>
                    <CardContent>
                        <Typography paragraph>
                             Responsible for maintaining/Bug fixing and developing UI
                            features .
                        </Typography>
                        <Typography paragraph>
                             Developed web application using React.js along with material UI
                            , Tailwind , HTML, CSS, LESS, Typescript and Node.js
                        </Typography>
                        <Typography paragraph>
                             Ensure adaptability across devices and web accessibility
                            standards for an inclusive user experience.
                        </Typography>
                        <Typography>
                             Managed team resources to deliver on sprint goals within
                            expected timeframes.
                        </Typography>
                        <Typography>
                             Contributed to sprint planning and prioritized backlogs.
                        </Typography>
                    </CardContent>
                </Collapse>
            </Card>
        </div>
    );
}